import React from "react";

interface ITile {
  text: string;
  color: string;
}
export default function Tile(props: ITile) {
  const { text, color } = props;
  return (
    <div
      style={{
        backgroundColor: color,
        width: "100%",
        height: "100%",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        fontSize: "14px",
      }}
    >
      {text}
    </div>
  );
}
