import { StoreType } from "@/store";
import { setConfig, setData } from "@/store/editor/action";
import { ARENA_OBJECT_TYPE, IData } from "@/store/editor/reducer";
import { resetMiniMap } from "@/utils";
import exportJson from "@/utils/exportJson";
import React from "react";
import { useDispatch, useSelector } from "react-redux";

const NavBar = () => {
  const dispatch = useDispatch();
  const arenaData = useSelector<StoreType, IData | unknown>(
    (state) => state.editor.data
  );

  const handleChange = (e) => {
    const fileReader = new FileReader();
    fileReader.readAsText(e.target.files[0], "UTF-8");
    fileReader.onload = (e) => {
      dispatch(setData(JSON.parse(e.target.result as string)));
      resetMiniMap();
    };
  };
  const clearArena = () => {
    dispatch(setData({}));
    (document.getElementById("uploadFile") as HTMLInputElement).value = "";
    resetMiniMap();
  };
  const onChangeSelected = (e) => {
    dispatch(setConfig({ selectedType: Number(e.target.value) }));
  };
  return (
    <div id="nav" style={{ marginBottom: "30px" }}>
      <button onClick={clearArena} style={{ marginBottom: "10px" }}>
        New Arena
      </button>
      <br />
      Import:{" "}
      <input
        id="uploadFile"
        type="file"
        onChange={handleChange}
        style={{ marginBottom: "10px" }}
      />
      <br />
      Edit:{" "}
      <select onChange={onChangeSelected}>
        <option key={ARENA_OBJECT_TYPE.NONE} value={ARENA_OBJECT_TYPE.NONE}>
          Normal Tile
        </option>
        <option
          key={ARENA_OBJECT_TYPE.GRASS_OBSTACLE}
          value={ARENA_OBJECT_TYPE.GRASS_OBSTACLE}
        >
          Obstcale (Grass)
        </option>
        <option
          key={ARENA_OBJECT_TYPE.SPAWN_SPOTS}
          value={ARENA_OBJECT_TYPE.SPAWN_SPOTS}
        >
          Spawn Point
        </option>
        <option
          key={ARENA_OBJECT_TYPE.TOXIC_CENTER}
          value={ARENA_OBJECT_TYPE.TOXIC_CENTER}
        >
          Shrink Target
        </option>
      </select>
      <button
        onClick={() => exportJson(JSON.stringify(arenaData), `worm_arena.json`)}
        style={{ marginLeft: "10px" }}
      >
        Export to JSON
      </button>
    </div>
  );
};

export default NavBar;
