import { Action } from 'redux';
import { COUNT_ADD, COUNT_SUB } from './constant';

const INIT_STATE = 0;

interface CountAction extends Action {
  payload: number;
}

function countReducer(state = INIT_STATE, action: CountAction): number {
  const { type, payload } = action;
  switch (type) {
    case COUNT_ADD:
      return state + payload;
    case COUNT_SUB:
      return state - payload;
    default:
      return state;
  }
}

export default countReducer;
