import React, { useState } from "react";
import Tile from "@/components/Tile";
import styles from "./index.module.scss";
import { useDispatch, useSelector } from "react-redux";
import { StoreType } from "@/store";
import { IConfig, IData } from "@/store/editor/reducer";
import { setData } from "@/store/editor/action";
import { resetMiniMap } from "@/utils";

export default function Grid() {
  const arenaData = useSelector<StoreType, IData | unknown>(
    (state) => state.editor.data
  );
  const config = useSelector<StoreType, IConfig>(
    (state) => state.editor.config as IConfig
  );
  const [isMouseDown, setIsMouseDown] = useState(false);

  const dispatch = useDispatch();

  const getKey = (i) => {
    if (typeof arenaData === "object") {
      for (const key in arenaData) {
        if (typeof arenaData[key] === "undefined") return;
        const id = arenaData[key].find((element) => element === i + 1);
        if (id && arenaData[key].includes(id)) {
          return String(key);
        }
      }
    }
    return "";
  };

  const changeData = (value: number, key: number) => {
    if (typeof arenaData === "object") {
      let data = { ...arenaData };
      if (key === 0) {
        if (typeof data[config.selectedType] !== "undefined") {
          data[config.selectedType].push(value);
          data[config.selectedType].sort(function (a, b) {
            return a - b;
          });
        } else {
          const obj = {
            [config.selectedType]: [value],
          };
          data = { ...data, ...obj };
        }
      } else {
        if (
          typeof arenaData[key] === "undefined" ||
          typeof data[config.selectedType] === "undefined"
        )
          return;
        arenaData[key].forEach((element, index) => {
          if (element === value) {
            data[key].splice(index, 1);
            data[config.selectedType].push(value);
            data[config.selectedType].sort(function (a, b) {
              return a - b;
            });
          }
        });
      }
      dispatch(
        setData({
          ...data,
        })
      );
      resetMiniMap();
    }
    return;
  };

  const getColor = (key) => {
    const normalTile = "#34a853";
    const obstacle = "#b7e0cd";
    const spawn = "#cccccc";
    const toxic = "#6fa8dc";
    switch (key) {
      case "2":
        return obstacle;
      case "3":
        return spawn;
      case "100":
        return toxic;

      default:
        return normalTile;
    }
  };

  const setColor = (e) => {
    changeData(
      Number(e.target.parentNode.dataset.tileId),
      Number(e.target.parentNode.dataset.tileValue)
    );
  };

  function renderSquare(i: number) {
    return (
      <div
        key={i}
        className={`${styles.tile} tile`}
        data-tile-color={getColor(getKey(i))}
        data-tile-id={i + 1}
        data-tile-value={getKey(i)}
        onMouseOver={(e) => isMouseDown && setColor(e)}
        onMouseDown={(e) => setColor(e)}
      >
        <Tile text={getKey(i)} color={getColor(getKey(i))} />
      </div>
    );
  }

  const squares = [];
  for (let i = 0; i < 6400; i += 1) {
    squares.push(renderSquare(i));
  }

  return (
    <div
      className={styles.board}
      onMouseDown={() => {
        setIsMouseDown(true);
      }}
      onMouseUp={() => {
        setIsMouseDown(false);
      }}
    >
      {squares}
    </div>
  );
}
