import React from 'react';
import ReactDOM from 'react-dom';
import { reactLifeCycles as singleSpaReact } from '@mkt-common/single-spa-utils';
import App from './App';
import store from './store';


const reactLifeCycles = singleSpaReact({
  React,
  ReactDOM,
  rootComponent: App,
  domElementId: process.env.SINGLE_SPA_ROOT_ELEMENT_ID
});

export const bootstrap = [
  reactLifeCycles.bootstrap,
];

export function mount(props) {
  const storeManager = props.storeManager;
  storeManager.registerStore(process.env.SINGLE_SPA_ROOT_ELEMENT_ID, store);
  return reactLifeCycles.mount(props);
}

export function unmount(props) {
  props.storeManager.unregisterStore(process.env.SINGLE_SPA_ROOT_ELEMENT_ID);
  return reactLifeCycles.unmount(props);
}
