import { Action } from 'redux';
import { EDITOR_CONFIG, EDITOR_DATA } from './constant';

const INIT_STATE = {
  config: {
    selectedType: 0
  },
  data: {}
};

export interface IConfig {
  selectedType: number;
}

export enum ARENA_OBJECT_TYPE {
  NONE = 0,
  GRASS_OBSTACLE = 2,
  SPAWN_SPOTS = 3,
  OCTAGON = 'A',
  TOXIC_CENTER = 100,
}

export interface IData {
  [ARENA_OBJECT_TYPE.GRASS_OBSTACLE]: number[];
  [ARENA_OBJECT_TYPE.SPAWN_SPOTS]: number[];
  [ARENA_OBJECT_TYPE.TOXIC_CENTER]: number[];
}

export interface EditorState {
  config: IConfig;
  data: IData | unknown;
}

interface EditorAction extends Action {
  payload: EditorState;
}

function editorReducer(state = INIT_STATE, action: EditorAction) {
  const { type, payload } = action;
  switch (type) {
    case EDITOR_CONFIG:
      return {...state, config: payload};
    case EDITOR_DATA:
      return {...state, data: payload};
    default:
      return state;
  }
}

export default editorReducer;
